.message {
    margin-bottom: 30px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 48px;
}

.text {
    display: inline-block;
    border-radius: 10px;
    padding: 0 24px 0 14px;
    background-color: #06C;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
    width: 522px;
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}

.text:after {

}

.name {
    text-align: right;
    margin-top: 8px;
    font-weight: 600;
    line-height: 32px;
}

.messageText {
    text-align: left;
    white-space: normal;
    word-wrap: break-word;
    font-weight: 400;
    margin-bottom: 8px;
}

.time {
    text-align: right;
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    font-weight: 600;
}