.friendMessage {
    margin-bottom: 30px;
}

.friendImageAndText {
    display: flex;
    flex-direction: row;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 48px;
}

.friendText {
    display: inline-block;
    border-radius: 10px;
    padding: 0 14px 0 24px;
    background-color: #F5F7FB;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}

.friendText:after {

}

.friendName {
    margin-top: 8px;
    font-weight: 600;
    line-height: 32px;
}

.friendMessageText {
    font-weight: 400;
    white-space: normal;
    word-wrap: break-word;
    margin-bottom: 8px;
}

.friendTime {
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    font-weight: 600;
}
